@mixin ripple-base($opacity: .6) {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  // :focus æè·åç¦ç¹æ¶ï¼å¯ä»¥éè¿ tab é®åæ¢æé¼ æ ç¹å»è§¦åè¯¥ä¼ªç±»ï¼è :active æé¼ æ ç¹å»æé®çæä¸æ¶è§¦å
  // ä¼ªç±» :active æ¶èæ¯å é
