@import "../../../../components/theme/variable";

.card-demo {
  .col {
    flex-grow: 0;
  }
  .card {
    width: 320px;
  }

  &-avatar {
    background-color: $grey-color-400;
  }

  &-img {
    background-image:  url("data:image/png;base64,");
    background-repeat: no-repeat;
    background-size:   cover;
  }

  &-bg {
    height:            250px;
    background-image:  url("data:image/png;base64,");
    background-repeat: no-repeat;
    background-size:   cover;

    .card-body,
    .card-actions {
      color:      $white;
      background: rgba(0, 0, 0, 0.4);

      .card-subtitle,
      .card-action {
        color: inherit;
        &:hover {
          color: inherit;
        }
      }
    }
  }
}


